import './style.scss';

const ClickableList = ( { items, onClick } ) => {

    const formatText = (i) => {
        switch(i.type){
            case 'component':
                return `<${i.text} />`;
            default: 
                return `${i.text}()`
        }
    }

	return (
		<ul className="gutenbuild-clickable-list">
			{ items.map( ( i ) => {
                const split = i.text.split('.');
                const text = split.pop();

            return(
				<li key={ i.text }>
					<a href="#" onClick={ () => onClick(i) }>
						<div className="gutenbuild-clickable-list__title">{ formatText(text) }</div>
                        {<span key={i} className="gutenbuild-clickable-list__subtitle">{split.join('.')}</span>}
					</a>
				</li>
			) } ) }
		</ul>
	);
};

export default ClickableList;
